import React, { useReducer, useContext } from "react";

const StoreStateContext = React.createContext();
const StoreDispatchContext = React.createContext();

const initial_state = {
    timer_readout: 0,
    config: {
        workout_name: "Tabata Classic",
        warmup: 30,
        cooldown: 30,
        rest: 10,
        work: 20,
        intervals: 8,
        sets: 2
    }
}

function reducer(state, action) {
    switch(action.type) {
        case "SET_CONFIG": return {
            ...state,
            config: {
                ...state.config,
                [action.payload.id]: action.payload.value
            }
        };
        case "SET_TIMER": return {
            ...state,
            timer_readout: action.payload.value
        };
        default:
            return state;
    }
}

function StoreProvider({ children }){
    const [state, dispatch] = useReducer(reducer, initial_state);

    return (
        <StoreStateContext.Provider value={state}>
            <StoreDispatchContext.Provider value={dispatch}>
                {children}
            </StoreDispatchContext.Provider>
        </StoreStateContext.Provider>
    );
}

function useStoreState() {
    const context = useContext(StoreStateContext);
    if(context === undefined) {
        throw new Error("useStoreState must be used within a StoreProvider.");
    }
    return context;
}

function useStoreDispatch() {
    const context = useContext(StoreDispatchContext);
    if(context === undefined) {
        throw new Error("useStoreDispatch must be used within a StoreProvider.");
    }
    return context
}

export { StoreProvider, useStoreState, useStoreDispatch };